import { useState, useEffect } from 'react';
import { useRouter } from 'next/router';
import CustomerLayout from '../../components/customer/CustomerLayout';
import { useAuth } from '../../context/AuthContext';
import { useToast } from '../../components/ui/ToastProvider';

export default function EditAddressPage() {
    const router = useRouter();
    const { id } = router.query; // If ID exists, we are editing
    const { showToast } = useToast();
    const { signOut, onboardingStep, getRouteForStep, setOnboardingStep, setOnboardingAddress } = useAuth();

    // Form State
    // Simplified Form State for UI
    const [uiForm, setUiForm] = useState({
        flatNo: '',
        floorNo: '',
        blockName: '',
        community: 'Community Cart',
        pincode: '',
        city: 'Hyderabad', // Default or derived
        state: 'Telangana', // Default or derived
        is_primary: true // Always primary for onboarding
    });

    const [isManualMode, setIsManualMode] = useState(false); // Fallback to manual entry if needed, but requirements say Enforce

    // Legacy Form State (for backward compat if editing existing)
    const [formData, setFormData] = useState({
        line1: '',
        line2: '',
        area: '',
        city: '',
        state: '',
        pincode: '',
        is_primary: false
    });
    const [loading, setLoading] = useState(false);
    const [pageLoading, setPageLoading] = useState(false);

    // ROUTE GUARD: Enforce onboarding step requirements
    useEffect(() => {
        const mode = router.query.mode as string;
        const isOnboarding = mode === 'onboarding';

        if (isOnboarding) {
            const allowedSteps = ['PHONE_VERIFIED', 'ADDRESS_PENDING'];

            if (onboardingStep && !allowedSteps.includes(onboardingStep)) {
                // User is at wrong step - redirect to correct route
                const correctRoute = getRouteForStep(onboardingStep);
                router.replace(correctRoute);
            } else if (!onboardingStep) {
                // No onboarding step set - shouldn't be in onboarding mode
                router.replace('/customer/signin');
            } else if (onboardingStep === 'PHONE_VERIFIED') {
                // Set to ADDRESS_PENDING when entering address page
                setOnboardingStep('ADDRESS_PENDING');
            }
        }
    }, [onboardingStep, router, getRouteForStep, setOnboardingStep]);

    useEffect(() => {
        if (id) {
            setPageLoading(true);
            // Fetch existing address details
            fetch('http://localhost:5000/api/customers/addresses', { credentials: 'include' })
                .then(res => res.json())
                .then((addresses: any[]) => {
                    const addr = addresses.find(a => a._id === id);
                    if (addr) {
                        setFormData({
                            line1: addr.line1 || '',
                            line2: addr.line2 || '',
                            area: addr.area || '',
                            city: addr.city || '',
                            state: addr.state || '',
                            pincode: addr.pincode || '',
                            is_primary: addr.is_primary
                        });
                    } else {
                        showToast('Address not found', 'error');
                        router.push('/customer/address');
                    }
                })
                .catch(err => console.error(err))
                .finally(() => setPageLoading(false));
        }
    }, [id, router]);

    const handleChange = (field: string, value: any) => {
        setFormData(prev => ({ ...prev, [field]: value }));
    };

    const handleUiChange = (field: string, value: any) => {
        setUiForm(prev => ({ ...prev, [field]: value }));
    };

    const handleSubmit = async () => {
        const mode = router.query.mode as string;
        const isOnboarding = mode === 'onboarding';

        // Validate address fields
        if (!id && !isManualMode) {
            if (!uiForm.flatNo || !uiForm.floorNo || !uiForm.blockName || !uiForm.community) {
                showToast('Please fill all address fields', 'error');
                return;
            }
        }

        setLoading(true);
        try {
            if (isOnboarding) {
                // ONBOARDING MODE: Save address locally only (no backend call)
                // User is NOT authenticated, so we cannot call auth-protected API

                // Save address to local state
                setOnboardingAddress({
                    flatNo: uiForm.flatNo,
                    floorNo: uiForm.floorNo,
                    blockName: uiForm.blockName,
                    community: uiForm.community,
                    pincode: uiForm.pincode || '500000',
                    city: uiForm.city,
                    state: uiForm.state
                });

                showToast('Address saved', 'success');

                // Mark onboarding as complete
                setOnboardingStep('ONBOARDING_COMPLETE');

                // Intentional logout after onboarding completion
                signOut();

                router.push('/customer/signin');
            } else {
                // NORMAL MODE: Save to backend (user is authenticated)
                // Prepare payload
                let payload: any = { ...formData };

                // If in specific UI mode, map to backend schema
                if (!id && !isManualMode) {
                    // Construct Line 1: "Flat 101, Floor 2, Block A"
                    payload.line1 = `Flat ${uiForm.flatNo}, Floor ${uiForm.floorNo}, ${uiForm.blockName}`;
                    payload.line2 = uiForm.community;
                    payload.area = uiForm.community;
                    payload.city = uiForm.city;
                    payload.state = uiForm.state;
                    payload.pincode = uiForm.pincode || '500000';
                    payload.is_primary = true;
                }

                // Basic validation
                if (!payload.line1 || !payload.city) {
                    showToast('Please fill required fields', 'error');
                    return;
                }

                const url = id
                    ? `http://localhost:5000/api/customers/addresses/${id}`
                    : 'http://localhost:5000/api/customers/addresses';

                const method = id ? 'PUT' : 'POST';

                const res = await fetch(url, {
                    method,
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify(payload),
                    credentials: 'include'
                });

                const data = await res.json();

                if (res.ok) {
                    showToast(id ? 'Address updated' : 'Address added', 'success');
                    router.push('/customer/address');
                } else {
                    showToast(data.message || 'Operation failed', 'error');
                }
            }
        } catch (error) {
            showToast('Server error', 'error');
        } finally {
            setLoading(false);
        }
    };

    if (pageLoading) return <div style={{ padding: 20 }}>Loading...</div>;

    return (
        <CustomerLayout disablePadding={true}>
            <div className="address-page-container">
                {/* Header */}
                <header className="address-header">
                    {router.query.mode !== 'onboarding' && (
                        <button
                            className="address-back-btn"
                            onClick={() => router.back()}
                        >
                            <img src="/customer/assets/icons/backward.svg" alt="Back" width={24} height={24} />
                        </button>
                    )}
                    <h1 className="address-title">{router.query.mode === 'onboarding' ? 'Add Delivery Address' : (id ? 'Edit Address' : 'Add Address')}</h1>
                </header>

                <div style={{ height: '32px' }}></div>

                {/* Editable Form */}
                <div className="address-form">
                    {!id ? (
                        /* NEW ADDRESS / ONBOARDING FORM */
                        <>
                            <div className="address-input-group">
                                <div className="address-input-wrapper">
                                    <label className="address-label">Community *</label>
                                    <input
                                        type="text"
                                        className="address-input"
                                        value={uiForm.community}
                                        readOnly // Fixed to Community Cart for now as per req
                                        style={{ backgroundColor: '#f5f5f5' }}
                                    />
                                </div>
                            </div>

                            <div className="address-input-group">
                                <div className="address-input-wrapper">
                                    <label className="address-label">Block Name *</label>
                                    <input
                                        type="text"
                                        className="address-input"
                                        value={uiForm.blockName}
                                        onChange={(e) => handleUiChange('blockName', e.target.value)}
                                        placeholder="e.g. Block A"
                                    />
                                </div>
                            </div>

                            <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '16px' }}>
                                <div className="address-input-group">
                                    <div className="address-input-wrapper">
                                        <label className="address-label">Floor *</label>
                                        <input
                                            type="text"
                                            className="address-input"
                                            value={uiForm.floorNo}
                                            onChange={(e) => handleUiChange('floorNo', e.target.value)}
                                            placeholder="e.g. 5"
                                        />
                                    </div>
                                </div>
                                <div className="address-input-group">
                                    <div className="address-input-wrapper">
                                        <label className="address-label">Flat No *</label>
                                        <input
                                            type="text"
                                            className="address-input"
                                            value={uiForm.flatNo}
                                            onChange={(e) => handleUiChange('flatNo', e.target.value)}
                                            placeholder="e.g. 504"
                                        />
                                    </div>
                                </div>
                            </div>

                            {/* Hidden/Default Fields */}
                            {/* Pincode optional in UI if derived, but let's ask for now or hide if hardcoded */}
                        </>
                    ) : (
                        /* EDIT MODE (Legacy fields) */
                        <>
                            <div className="address-input-group">
                                <div className="address-input-wrapper">
                                    <label className="address-label">Address Line 1 *</label>
                                    <input
                                        type="text"
                                        className="address-input"
                                        value={formData.line1}
                                        onChange={(e) => handleChange('line1', e.target.value)}
                                        placeholder="House No, Building Name"
                                    />
                                </div>
                            </div>
                            {/* ... other legacy fields ... */}
                            <div className="address-input-group">
                                <div className="address-input-wrapper">
                                    <label className="address-label">Address Line 2</label>
                                    <input
                                        type="text"
                                        className="address-input"
                                        value={formData.line2}
                                        onChange={(e) => handleChange('line2', e.target.value)}
                                        placeholder="Street, Landmark"
                                    />
                                </div>
                            </div>

                            <div className="address-input-group">
                                <div className="address-input-wrapper">
                                    <label className="address-label">Area</label>
                                    <input
                                        type="text"
                                        className="address-input"
                                        value={formData.area}
                                        onChange={(e) => handleChange('area', e.target.value)}
                                    />
                                </div>
                            </div>

                            <div className="address-input-group">
                                <div className="address-input-wrapper">
                                    <label className="address-label">City *</label>
                                    <input
                                        type="text"
                                        className="address-input"
                                        value={formData.city}
                                        onChange={(e) => handleChange('city', e.target.value)}
                                    />
                                </div>
                            </div>

                            <div className="address-input-group">
                                <div className="address-input-wrapper">
                                    <label className="address-label">Pincode *</label>
                                    <input
                                        type="text"
                                        className="address-input"
                                        value={formData.pincode}
                                        onChange={(e) => handleChange('pincode', e.target.value)}
                                    />
                                </div>
                            </div>

                            {/* Primary Switch - Only show if current address is NOT primary (can't unset primary directly) OR if adding new */}
                            {(!id || !formData.is_primary) && (
                                <div className="address-input-group" style={{ marginTop: 20, flexDirection: 'row', alignItems: 'center', justifyContent: 'space-between' }}>
                                    <label className="address-label" style={{ marginBottom: 0 }}>Set as Primary Address</label>
                                    <input
                                        type="checkbox"
                                        checked={formData.is_primary}
                                        onChange={(e) => handleChange('is_primary', e.target.checked)}
                                        style={{ width: 20, height: 20 }}
                                    />
                                </div>
                            )}
                            {id && formData.is_primary && (
                                <div style={{ marginTop: 10, fontSize: '12px', color: '#666' }}>
                                    This is your primary address. Set another address as primary to change this.
                                </div>
                            )}
                        </>
                    )}

                </div>

                {/* CTA */}
                <div className="address-cta-container">
                    <button
                        className="address-cta-btn touchable"
                        onClick={handleSubmit}
                        disabled={loading}
                    >
                        {loading ? 'Saving...' : (id ? 'Update Address' : 'Save Address')}
                    </button>
                </div>
            </div>
        </CustomerLayout>
    );
}
